@media screen and (max-width: 1024px){
    .card{
        display: flex;
        justify-content: space-around;
        
    }

    .front{
        display: flex;
        flex-wrap: wrap;
        margin-left: 600px;
    }

    .front img{
        width: 200px;
        height: 200px;
        margin-right: 1rem;
    }
}

