
.container {
    position: fixed;
    width: 100%;
    z-index: 10;
}

.title ul{
    display: flex;
    align-items: center;
    gap:10px;
    list-style: none;
    
    
}

.container .title ul li {
    text-decoration: none;
    color: #fff;
    cursor: pointer;   
}


.container .title ul li:hover{
    color: #FF3D33;
    transition: color 1s;
    cursor: pointer;
}


.burger{
    display: none;
    
}





@media screen and (max-width: 720px){
        .search{
            display: none;
        }
        .burger{
            display: flex;
        }
    
        .nav-menu{
            position: fixed;
            left:-100%;
            top: 70px;
            flex-direction: column;
            justify-content: center;
            width: 100%;
            height: 100%;
            text-align: center;
            transition: 2s ease;
            background-color: #000c66;
            margin-top: -11px;
            z-index:1;
        }
    
        .nav-menu.active{
            left: 0;
        }
    
        .nav-item{
            margin: 3px;
            font-size: 20px;
            justify-content: center;
        }
    
        li a{
            color: #fff;
        }

        li a:hover{
            color: aquamarine;
            transition: 4s;
        }

        .home{
            display: none;
        }
        
    }
